<template>
  <div>
    <div class="page-subtitle">
      <h2 class="center">Add new photo</h2>
    </div>
    <div class="file-field input-field">
      <div class="btn">
        <span>File</span>
        <input
          type="file"
          class="input-upload"
          @change="onFileChange"
        >
      </div>
      <div class="file-path-wrapper">
        <input class="file-path validate" type="text">
      </div>
    </div>
    <div class="showUploadPhotoDiv">
      <img
        v-if="image"
        :src="image"
        class="photo responsive-img center"
        alt="post-image"
      />
      <a
        class="btn-floating btn-large waves-effect waves-light red"
        @click="uploadPhoto"
        v-if="image"
      ><i class="material-icons">add</i></a>
    </div>
  </div>
</template>

<script>

export default {
  data: () => ({
    image: '',
    imageName: ''
  }),
  methods: {
    onFileChange(e, type) {
      const file = e.target.files || e.dataTransfer.files;
      const reader = new FileReader();

      reader.onload = (e) => {
        this.image = e.target.result;
      };

      this.imageName = file[0].name

      reader.readAsDataURL(file[0]);
    },
    async uploadPhoto() {
      const data = {
        id: Date.now(),
        name: this.imageName,
        url: this.image
      }
      await this.$store.dispatch('insertPhoto', data)
      this.$message('The new photo was added successfully!')
      this.$emit('newPhoto', data)
      this.imageName = ''
      this.image = ''
    }
  }
}
</script>

<style lang="scss">
.photo {
  width: 400px;
  height: 550px;
}

.page-subtitle {
  margin-bottom: 40px;
}

.showUploadPhotoDiv {
  display: flex;
  flex-direction: column;
  align-items: center;
}
</style>
