<template>
  <div class="home">
    <div class="row">
      <AddPhoto @newPhoto="addedNewPhoto"/>
      <Loader v-if="loading"/>
      <p class="center" v-else-if="!photos.length">There are no any photo yet!</p>
      <ShowPhoto
        v-else
        :photos="photos"
        @deletedPhoto="updateAfterDelete"
        @deletedAllPhotos="deletedAllPhotos"
      />
    </div>

  </div>
</template>

<script>
import AddPhoto from "../components/AddPhoto";
import ShowPhoto from "../components/ShowPhoto";
export default {
  name: 'Home',
  data: () => ({
    loading: true,
    photos: []
  }),
  async created() {
    try {
      this.photos = await this.$store.dispatch('fetchPhoto')
      this.loading = false
    } catch (e) {}
  },
  async mounted() {

  },
  methods: {
    addedNewPhoto(newPhoto) {
      this.photos.push(newPhoto)
    },
    updateAfterDelete(index) {
      let photo = [...this.photos]
      photo.splice(index,1)
      this.photos = photo
    },
    deletedAllPhotos() {
      this.photos = []
    }
  },
  components: {
    AddPhoto, ShowPhoto
  }
}
</script>

<style lang="scss">
.page-subtitle h2 {
  color: #26a69a!important;
}

.row p {
  color: #b33e3e;
  font-size: 20px;
}
</style>
