<template>
  <div>
    <div  class="page-subtitle">
      <h2 class="center">Portfolio</h2>
    </div>
    <div class="deleteAllDiv" @click="deleteAllPhotos">
      <i
        class="small material-icons"
      >delete</i>
      <span>Delete All</span>
    </div>
    <div class="center imgDiv">
      <div class="img-wrap"
           v-for="(photo,index) in photos"
           :key="photo.id"
      >
        <i
          class="small material-icons close"
          :key="photo.id"
          @click="deletePhoto(photo.id, index)"
        >close</i>
        <img
          :src="photo.url"
          :alt="photo.name"
          class="photo"
          @click="showPhotoModal(photo.url)"
        >
      </div>
    </div>


    <div
      class="showPhotoModal"
      v-show="current"
    >
      <i
        class="small material-icons close"
        @click="closePhoto"
      >close</i>
      <img :src="current">
    </div>
  </div>
</template>

<script>
export default {
  props: {
    photos: {
      type: Array,
      required: true
    },
  },
  data: () => ({
    current: ''
  }),
  methods: {
    async deletePhoto(id, index) {
      try {
        await this.$store.dispatch('deletePhotoById', {id})
        this.$message('The photo deleted successfully!')
        this.$emit('deletedPhoto', index)
      } catch (e) {}
    },
    async deleteAllPhotos() {
      try {
        await this.$store.dispatch('deleteAllPhotos', this.photos)
        this.$message('The all photos where deleted successfully!')
        this.$emit('deletedAllPhotos')
      } catch (e) {}
    },
    showPhotoModal(url) {
      this.current = url
    },
    closePhoto() {
      this.current = ''
    }
  }
}
</script>

<style lang="scss">
.photo {
  width: 300px;
  height: 150px;
  margin-right: 20px;
  margin-bottom: 20px;
  object-fit: cover;
  cursor: zoom-in;
}

.img-wrap {
  position: relative;
}
.img-wrap .close {
  position: absolute;
  top: 2px;
  right: 20px;
  z-index: 100;
  color: #b33e3e;
  cursor: pointer;
}

.imgDiv {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 20px;
}

.deleteAllDiv {
  align-items: center;
  display: inline-block;
  cursor: pointer;
  margin-left: 15px;
}

  .deleteAllDiv i {
    color: #b33e3e;
  }

  .deleteAllDiv span {
    font-size: 20px;
    color: #b33e3e;
    display: inline-block;
    line-height: 38px;
    vertical-align: bottom;
  }

.showPhotoModal {
  position: fixed;
  display: inline-flex;
  top: 5%;
  left: calc((100% - 400px) / 2);
  z-index: 105;
  width: 400px;
}

  .showPhotoModal img {
    width: 100%;
  }

  .showPhotoModal .close {
    position: absolute;
    top: 2px;
    right: 20px;
    z-index: 100;
    color: #b33e3e;
    cursor: pointer;
  }
</style>
